<template>
    <div class="m-publish-box">
        <!-- Â§¥ÈÉ® -->
        <pubheader name="ÊàêÂ∞±ÁôæÁßë" :localDraft="false">
            <slot name="header"></slot>
        </pubheader>

        <h1 class="m-publish-cj-header">Ë¥°ÁåÆÊîªÁï•</h1>
        <el-form
                label-position="left"
                label-width="80px"
                class="m-publish-cj"
        >
            <!-- üíõ Ê†èÁõÆÂ≠óÊÆµ -->
            <el-form-item label="ÊàêÂ∞±ÈÄâÊã©">
                <el-select
                    class="u-achievement_id"
                    v-model="post.achievement_id"
                    filterable
                    remote
                    :disabled="!!post.id"
                    placeholder="ËæìÂÖ•ÊàêÂ∞±ÂêçÁß∞/ÊàêÂ∞±ÊèèËø∞/Áß∞Âè∑/Â•ñÂä±Áâ©ÂìÅÂπ∂Êåâ„ÄéÂõûËΩ¶„ÄèËøõË°åÊêúÁ¥¢"
                    :filter-method="search_achievements_handle"
                    :loading="options.search_loading"
                >
                    <el-option
                        v-for="item in options.achievements"
                        :key="item.ID"
                        :label="item.Name"
                        :value="item.ID"
                    >
                        <div class="m-selector-item">
                            <img
                                class="u-icon"
                                :src="icon_url_filter(item.IconID)"
                                :alt="item.Name"
                            />
                            <span class="u-name" v-text="item.Name"></span>
                        </div>
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="ÁªºÂêàÈöæÂ∫¶">
                <el-rate v-model="post.level" class="u-level"></el-rate>
            </el-form-item>

            <el-form-item label="‰øÆËÆ¢ËØ¥Êòé">
                <el-input
                    v-model="post.remark"
                    placeholder="ËØ∑ÁÆÄÂçïÊèèËø∞‰∏Ä‰∏ãÊú¨Ê¨°‰øÆËÆ¢ÁöÑËØ¥Êòé"
                ></el-input>
            </el-form-item>

            <el-form-item label="ÊîªÁï•Ê≠£Êñá" class="m-publish-cj-content">
                <Tinymce
                        v-model="post.content"
                        :attachmentEnable="true"
                        :resourceEnable="true"
                        :height="400"
                />
                <el-button
                        class="u-publish"
                        icon="el-icon-s-promotion"
                        type="success"
                        @click="toPublish"
                        :disabled="processing"
                >Êèê‰∫§ÊîªÁï•</el-button
                >
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import pubheader from "@/components/publish/pubheader.vue";
import Tinymce from '@jx3box/jx3box-editor/src/Tinymce'

// Êú¨Âú∞‰æùËµñ
import { $ as $http } from "../service/axios";
import { JX3BOX } from "@jx3box/jx3box-common";

const { User } = require("@jx3box/jx3box-common");
const qs = require("qs");
const lodash = require("lodash");

export default {
    name: "achievement",
    props: [],
    data: function() {
        return {
            //ÈÄâÈ°π - Âä†ËΩΩÂèØÈÄâÈ°π
            options: {
                achievements: null,
                search_loading: false,
            },

            //ÊñáÁ´† - ‰∏ªË°®Êï∞ÊçÆ
            post: {
                id: "",     // ÊñáÁ´†ID
                content: "",
                achievement_id: "",
                level: 0,
                remark: "",
            },

            // Êâ©Â±ï - ÈÉ®ÂàÜÊ†èÁõÆÊñáÁ´†‰∏çÂ∫îÂêØÁî®ËØ•ÂäüËÉΩ
            extend: {
                feedEnable: false, //ÊòØÂê¶ÈÄöÁü•ËÆ¢ÈòÖÁî®Êà∑
                followEnable: false, //ÊòØÂê¶ÈÄöÁü•Á≤â‰∏ù
                tencentEnable: false, //ÊòØÂê¶ÂêåÊ≠•Ëá≥ËÖæËÆØÊñáÊ°£
                weiboEnable: false, //ÊòØÂê¶ÂêåÊ≠•Ëá≥ÂæÆÂçöÂ§¥Êù°ÊñáÁ´†
                tuilanEnable: false, //ÊòØÂê¶ÂêåÊ≠•Ëá≥Êé®Ê†è
            },
        };
    },
    computed: {
        processing:function (){
            return this.$store.state.processing
        }
    },
    methods: {
        toPublish: function() {
            if (!this.post.achievement_id) {
                this.$message({
                    message: "ËØ∑ÈÄâÊã©Ë¶Å‰øÆËÆ¢ÊîªÁï•ÁöÑÊàêÂ∞±",
                    type: "warning",
                });
                return;
            }

            if (!this.post.content) {
                this.$message({ message: "Ë¶ÅÁºñÂÜôÊîªÁï•Ê≠£ÊñáÂì¶", type: "warning" });
                return;
            }

            if (!(this.post.level >= 1 && this.post.level <= 5)) {
                this.$message({message: "ËØ∑ÈÄâÊã©ÈÄÇÂêàÁöÑÁªºÂêàÈöæÂ∫¶", type: "warning"});
                return;
            }

            if (!this.post.remark) {
                this.$message({
                    message: "ËØ∑ÁÆÄÂçïÊèèËø∞Êú¨Ê¨°‰øÆËÆ¢ËØ¥Êòé",
                    type: "warning",
                });
                return;
            }

            this.$store.commit('startProcess');
            $http({
                method: "POST",
                url: `${JX3BOX.__helperUrl}api/achievement/${this.post.achievement_id}/post`,
                headers: { Accept: "application/prs.helper.v2+json" },
                data: qs.stringify({
                    post: {
                        level: this.post.level,
                        user_nickname: User.getInfo().name,
                        content: this.post.content,
                        remark: this.post.remark,
                    },
                }),
            })
                .then((data) => {
                    data = data.data;
                    if (data.code === 200) {
                        this.$message({
                            message: "Êèê‰∫§ÊàêÂäüÔºåËØ∑Á≠âÂæÖÂÆ°Ê†∏",
                            type: "success",
                            onClose: () => {
                                this.$router.go(0);
                            },
                        });
                    } else {
                        this.$message({
                            message: `${data.message}`,
                            type: "warning",
                        });
                    }
                })
        },
        get_achievement_newest_post(achievement_id) {
            return $http({
                url: `${JX3BOX.__helperUrl}api/achievement/${achievement_id}/post`,
                headers: {Accept: "application/prs.helper.v2+json"},
            }).catch((err) => {
                resolve(false);
            });
        },
        icon_url_filter(icon_id) {
            if (isNaN(parseInt(icon_id))) {
                return `${JX3BOX.__imgPath}image/common/nullicon.png`;
            } else {
                return `${JX3BOX.__iconPath}icon/${icon_id}.png`;
            }
        },
        async search_achievements_handle(keyword) {
            this.options.search_loading = true;
            let data = await this.search_achievements(keyword, 10);
            this.options.achievements = data.achievements;
            this.options.search_loading = false;
        },
        // ÊàêÂ∞±ÊêúÁ¥¢
        search_achievements(keyword, length) {
            return new Promise((resolve, reject) => {
                $http({
                    method: "GET",
                    url: `${JX3BOX.__helperUrl}api/achievement/search`,
                    headers: { Accept: "application/prs.helper.v2+json" },
                    params: { keyword: keyword, limit: length },
                }).then(
                    function(data) {
                        data = data.data;
                        resolve(data.code === 200 ? data.data : false);
                    },
                    function() {
                        resolve(false);
                    }
                );
            });
        },
    },
    async mounted() {
        await this.search_achievements_handle("");

        // Ëé∑ÂèñÊàêÂ∞±IDÂπ∂ÈÄöËøáwatchËé∑ÂèñÊîªÁï•
        let id = this.$route.params.achievement_id;
        this.post.achievement_id = id ? parseInt(id) : null;

        // ÂéªÊéâÊ†áÈ¢ò
        document.getElementsByClassName("m-publish-title").forEach((item) => {
            item.remove();
        });
        // ÂéªÊéâËçâÁ®ø
        document.getElementsByClassName("el-button--plain").forEach((item) => {
            item.remove();
        });
    },
    watch: {
        "post.achievement_id": {
            handler() {
                if(!this.post.achievement_id) return;
                this.get_achievement_newest_post(this.post.achievement_id)
                    .then((res) => {
                    let data = res.data;
                    data = data.code === 200 ? data.data : false;

                    // Êï∞ÊçÆÂ°´ÂÖÖ
                    let post = data.post;
                    let achievement = data.achievement;
                    if (post) {
                        this.post.achievement_id = parseInt(post.achievement_id);
                        this.post.level = post.level || 1;
                        this.post.remark = "";
                        let content = post.content;
                        content = content.replace(/(<p>)?\s*‚óÜÊàêÂ∞±ÈöæÂ∫¶ [‚òÖ]+\s*(<\/p>)?/ig,'');
                        content = content.replace(/(<p>)?\s*‚óÜËä±Ë¥πÊó∂Èïø [‚òÖ]+\s*(<\/p>)?/ig,'');
                        content = content.replace(/(<p>)?\s*‚óÜÊàêÂ∞±ÊîªÁï•\s*(<\/p>)?/ig,'');
                        this.post.content = content;
                    } else {
                        this.post.achievement_id = this.post.achievement_id
                            ? parseInt(this.post.achievement_id)
                            : "";
                        this.post.level = 0;
                        this.post.remark = "";
                        this.post.content = "";
                    }

                    if (achievement) {
                        // Â∞ÜÈÄâÊã©È°πÊÅ¢Â§çËá≥‰∏ãÊãâÊ°Ü
                        let exist = false;
                        this.options.achievements = this.options.achievements || [];
                        for (let index in this.options.achievements) {
                            if (this.options.achievements[index].ID == this.post.achievement_id) {
                                exist = true;
                                break;
                            }
                        }
                        if (!exist) this.options.achievements.push(achievement);
                    }
                });
            },
        },
    },
    components: {
        pubheader,
        Tinymce,
    },
};
</script>

<style lang="less">
@import "../assets/css/publish/cj.less";
</style>
